@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;

  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}

@font-face {
  font-family: "PTSans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;

  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
